html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-Slogan-end {
  width: 6.23em;
  height: 1em;
  fill: auto;
}

.icon-Slogan-start {
  width: 5.96em;
  height: 1em;
  fill: #000;
}

.icon-facebook {
  width: 0.86em;
  height: 1em;
  fill: url(#paint0_linear);
}

.icon-mail {
  width: 0.86em;
  height: 1em;
  fill: url(#paint0_linear);
}

.icon-telegram {
  width: 0.98em;
  height: 1em;
  fill: url(#paint0_linear);
}

.icon-twitter {
  width: 0.87em;
  height: 1em;
  fill: url(#paint0_linear);
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: 'Montserrat', sans-serif;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.container {
  max-width: 940px;
  width: 100%;
  min-width: 300px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    padding: 0 20px;
  }
}

.about {
  width: 100%;
  background-color: #fff;
  font-weight: 700;
  padding-top: 55px;
  padding-bottom: 85px;
}

.about__title {
  font-size: 80px;
  line-height: 1.2;
  margin-bottom: 15px;
}

.about__text-wrapper {
  margin-bottom: 65px;
}

.about__text {
  color: rgba(0, 0, 0, 0.5);
  font-size: 24px;
  line-height: 1.25;
}

.about__slogan {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .about__slogan {
    flex-direction: column;
    align-items: center;
  }
}

.about__slogan-start, .about__img-wrapper, .about__slogan-end {
  display: inline-block;
}

@media only screen and (max-width: 768px) {
  .about__slogan-start,
  .about__slogan-end {
    width: 70%;
    margin: 0 auto 20px;
  }
}

@media only screen and (max-width: 490px) {
  .about__slogan-start,
  .about__slogan-end {
    width: 100%;
  }
}

.about__slogan-end {
  align-self: flex-start;
}

.about__img-wrapper {
  margin: 0 20px;
}

@media only screen and (max-width: 768px) {
  .about__img-wrapper {
    margin-bottom: 20px;
    width: 50%;
    margin: 0 auto 20px;
  }
}

@media only screen and (max-width: 530px) {
  .about__img-wrapper {
    width: 70%;
  }
}

.about__img {
  display: block;
  width: 55px;
  height: auto;
}

@media only screen and (max-width: 768px) {
  .about__img {
    width: 70%;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 530px) {
  .about__img {
    width: 100%;
  }
}

.about__slogan-img {
  display: block;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 768px) {
  .about__slogan-img {
    width: 70%;
  }
}

@media only screen and (max-width: 490px) {
  .about__slogan-img {
    width: 100%;
  }
}

.speakers {
  background-color: pink;
}

.contacts-wrapper {
  width: 100%;
  background-color: #000;
}

.contacts {
  font-weight: 700;
  padding-top: 55px;
  padding-bottom: 85px;
}

.contacts__title {
  color: #fff;
}

.contacts__text {
  color: #9A8787;
  font-size: 24px;
}

.contacts__text, .contacts__title {
  text-align: center;
  font-weight: 700;
}

.contacts__socials {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-top: 35px;
}

@media only screen and (max-width: 530px) {
  .contacts__socials {
    flex-direction: column;
    align-items: center;
  }
}
