.about {
	width: 100%;
	background-color: $white;
	font-weight: 700;
	padding-top: 55px;
	padding-bottom: 85px; }

.about__title {
	font-size: 80px;
	line-height: 1.2;
	margin-bottom: 15px; }

.about__text-wrapper {
	margin-bottom: 65px; }

.about__text {
	color: $about-text;
	font-size: 24px;
	line-height: 1.25; }

.about__slogan {
	display: flex;
	justify-content: center;
	align-items: center;

	@include r(768) {
		flex-direction: column;
		align-items: center; } }

.about__slogan-start, .about__img-wrapper, .about__slogan-end {
	display: inline-block; }

.about__slogan-start,
.about__slogan-end {
	@include r(768) {
		width: 70%;
		margin: 0 auto 20px; }

	@include r(490) {
		width: 100%; } }

.about__slogan-end {
	align-self: flex-start; }

.about__img-wrapper {
	margin: 0 20px;

	@include r(768) {
		margin-bottom: 20px;
		width: 50%;
		margin: 0 auto 20px; }

	@include r(530) {
		width: 70%; } }

.about__img {
	display: block;
	width: 55px;
	height: auto;

	@include r(768) {
		width: 70%;
		margin: 0 auto; }

	@include r(530) {
		width: 100%; } }

.about__slogan-img {
	display: block;
	width: 100%;
	margin: 0 auto;

	@include r(768) {
		width: 70%; }

	@include r(490) {
		width: 100%; } }
