.contacts-wrapper {
	width: 100%;
	background-color: $black; }


.contacts {
	font-weight: 700;
	padding-top: 55px;
	padding-bottom: 85px; }

.contacts__title {
  color: $white; }


.contacts__text {
  color: $contacts-text;
  font-size: 24px; }

.contacts__text, .contacts__title {
    text-align: center;
    font-weight: 700; }

.contacts__socials {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-top: 35px;
  flex-wrap:wrap {}

  @include r(530) {
    flex-direction: column;
    align-items: center; } }




